import Vue from 'vue';
import AMap from 'vue-amap';

Vue.use(AMap);
AMap.initAMapApiLoader({
  key: '38656eadcf51b8c8cdd3a93b34a9c315',
  plugin: ['Autocomplete','Geolocation', 'PlaceSearch', 'Scale', 'OverView', 'ToolBar', 'MapType', 'PolyEditor', 'AMap.CircleEditor'],
  // 默认高德 sdk 版本为 1.4.4
  v: '1.4.4'
});
export default {
  AMap
}



